import {Component, Input, OnInit} from '@angular/core';
import {Degree, Department, Teacher, Title} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-update-teacher',
  templateUrl: './update-teacher.component.html'
})
export class UpdateTeacherComponent implements OnInit {
  @Input() titles: Title[];
  departments: Department[];
  degrees: Degree[];
  teacher: Teacher;
  constructor(
    private route: ActivatedRoute,
    private teacherService: UniversityService,
    private location: Location
  ) {
  }

  ngOnInit() {
    this.getTitles();
    this.getDegrees();
    this.getDepartments(); // 下拉菜单赋值
    const id = this.route.snapshot.params.id; // 要修改专业的id
    this.teacherService.getTeacher(id)
      .subscribe(res => {
        this.teacher = res;
      });
  }
  compareFn1(o1: Title, o2: Title): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  compareFn2(o1: Degree, o2: Degree): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  compareFn3(o1: Department, o2: Department): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  getTitles(): void {
    this.teacherService.getTitles()
      .subscribe(res => this.titles = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }

  save(): void { // 多个文本框数据
    console.log(this.teacher);
    this.teacherService.updateTeacher(this.teacher)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }

}
