import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-prof-title',
  templateUrl: './add-prof-title.component.html',
  styleUrls: ['./add-prof-title.component.css']
})
export class AddProfTitleComponent implements OnInit {
  @Input() title: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private titleService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  save(description: string, no: string, remarks: string): void { //多个文本框数据
    this.title = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.titleService.addTitle(this.title)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
