import {Component, Input, OnInit} from '@angular/core';
import {Degree, Department, Teacher, Title, College} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-teacher',
  templateUrl: './add-teacher.component.html'
})
export class AddTeacherComponent implements OnInit {
  @Input() titles: Title[];
  departments: Department[];
  degrees: Degree[];
  colleges: College[];
  teacher: Teacher;
  title: Title;
  department: Department;
  degree: Degree;
  college: College;
  constructor(
    private route: ActivatedRoute,
    private teacherService: UniversityService,
    private location: Location
  ) {
  }

  ngOnInit() {
    this.getColleges();
    this.getTitles();
    this.getDepartments();
    this.getDegrees();
  }

  getColleges(): void{
    this.teacherService.getColleges()
      .subscribe(res => this.titles = res);
  }
  getTitles(): void {
    this.teacherService.getTitles()
      .subscribe(res => this.titles = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }
  getDepSch(id: number): void {
    this.teacherService.getDepSch(id)
      .subscribe(res => this.departments = res);
  }

  save(name: string, title: Title, degree: Degree, department: Department): void { //多个文本框数据
    this.teacher = {id: null, name: name.trim(), title: this.title,  degree: this.degree, department: this.department};
    if (!name) {
      return;
    }
    this.teacherService.addTeacher(this.teacher)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
